.header {
    background-color: rgb(218, 133, 14);
    /* height: 200px; */
  }
  
  .header h1 {
    margin: 0;
    padding-top: 75px;
    text-align: center;
    color: white;
    font-size: 4rem;
    font-weight: bold;
    font-family: 'Bungee Shade', cursive;
  }
  
